body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Georgia', serif;
}

.app {
  background-image: url('https://images.unsplash.com/photo-1618539200387-d3f4eaf4e393?ixlib=rb-4.0.3&auto=format&fit=crop&w=1950&q=80');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem;
}

.verse-card {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 2rem;
  border-radius: 1rem;
  max-width: 600px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.2);
}

.verse-text {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1rem;
}

.verse-ref {
  font-size: 1.2rem;
  color: #666;
}
